@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

body {
  font-family: 'Poppins', sans-serif;
  background: rgba(2, 0, 36, 0.941);
  background: linear-gradient(140deg, rgba(2, 0, 36, 0.9528186274509804) 0%, rgba(9, 9, 121, 0.888) 15%, rgba(7, 45, 144, 1) 43%, rgba(11, 102, 172, 0.935) 63%, rgba(0, 212, 255, 0.9023984593837535) 98%, rgb(129, 201, 246) 100%);
  height: 100vh;
}

.squares{

display: grid ;
column-gap: 2px;
row-gap: 2px;
grid-template-columns: 9vw 9vw 9vw;
grid-template-rows: 9vw 9vw 9vw;
justify-content: center;
margin-top: 0;

}

@media (max-width: 900px) or (max-height: 800px) {
 
  .squares{
    grid-template-columns: 100px 100px 100px;
    grid-template-rows: 100px 100px 100px;
  }
}
@media (min-width:1500px) {

  .squares {
    grid-template-columns: 130px 130px 130px;
    grid-template-rows: 130px 130px 130px;
  }
}
